
.fade-enter-active,.fade-leave-active{
  -webkit-transition: .3s all ease;
  transition: .3s all ease;
}
.fade-enter, .fade-leave-to{
  opacity: 0;
}

body {
  background: #7babc2;
}
.welcomePg {
  text-align: center;
  padding: 30px 0;
  color: #fff;
}
.good p {
  margin: 20px 0;
  font-size: 24px;
}
.info {
  width: 225px;
  margin: auto;
  line-height: 30px;
  border-right: #FFF 1px solid;
  border-left: #FFF 1px solid;
  padding: 10px 0;
}
.gdimg {
  margin: 30px 0 20px 0;
}
.gdimg img {
    width: 200px;
    border: 5px solid #fff;
}
.opentrip {
  width: 200px;
  margin: auto;
  border: #fff 1px solid;
  border-radius: 5px;
  font-size: 20px;
  font-weight: bold;
  line-height: 40px;
}
.opentrip a {
    color: #FFF;
    text-decoration: none;
    display: inline-block;
    width: 100%;
    line-height: 62px;
}

.homePage{
  max-width: 1200px;
  margin: 0 auto;
  background:#fff;
}
@charset "UTF-8";
