header h3 {
  color: gray;
  font-size: 28px;
  font-weight: normal;
  padding: 30px 0 10px;
  text-align: center; }
nav {
  border-top: 1px solid #bababa;
  border-bottom: 1px solid #bababa;
  text-align: center; }
nav ul {
    overflow: hidden;
    display: inline-block; }
nav ul li {
      float: left;
      padding: 8px 0;
      margin: 0 20px;
      font-size: 14px;
      color: #999;
      cursor: pointer;
      width: 60px;
      text-align: center; }
nav ul li:hover {
        color: #333; }
nav ul li a {
        color: #999; }
.homeContent .picArea {
  width: 90%;
  margin: 30px auto; }
.homeContent .picArea img {
    width: 100%; }
.homeContent .blogCon {
  overflow: hidden;
  width: 90%;
  margin: 0 auto;
  padding-bottom: 30px; }
.homeContent .blogCon .blogList {
    float: left;
    width: 75%; }
.homeContent .blogCon .blogTab {
    float: left;
    width: 25%; }
.blogList_ul {
  padding: 0 20px 20px;
  border-right: 1px #bababa dashed; }
.blogList_ul li {
    padding: 20px 0;
    border-bottom: 1px dotted #bababa; }
.blogList_ul li .time {
      color: #999;
      margin: 12px 0;
      font-size: 12px; }
.blogList_ul li .time label {
        margin-right: 20px; }
.blogList_ul li .describe {
      color: #999;
      font-size: 13px;
      word-break: break-all; }
.myPages {
  text-align: right;
  padding-right: 10px; }
.myPages ul {
    overflow: hidden;
    display: inline-block; }
.myPages ul li {
      float: left;
      border: 1px solid #bababa;
      color: #bababa;
      font-size: 12px;
      line-height: 12px;
      padding: 2px 6px;
      margin: 0 4px; }
.blogTab {
  padding: 10px 0 0 10px; }
.searchTitle {
  overflow: hidden; }
.searchTitle input {
    float: left;
    margin-right: 10px;
    width: calc(100% - 62px);
    padding-left: 6px; }
.searchTitle span {
    float: left; }
.rightBox {
  margin-top: 30px;
  border: 1px solid #bababa;
  border-radius: 4px; }
.rightBox .top {
    background: #f5f5f5;
    line-height: 30px;
    padding: 3px 10px;
    border-bottom: 1px solid #bababa;
    border-radius: 4px 4px 0 0;
    color: #333; }
.rightBox .blogType {
    padding: 10px 10px 0; }
.rightBox .blogType li {
      display: inline-block;
      background: #7babc2;
      color: #fff;
      padding: 3px 12px;
      margin-bottom: 10px;
      margin-right: 5px;
      cursor: pointer;
      border-radius: 8px;
      font-size: 14px; }
.rightBox .blogType li:hover {
        background: #4aabc2; }
.rightBox .newList {
    padding: 10px 10px 0; }
.rightBox .newList li {
      padding: 8px 0;
      font-size: 14px;
      color: #999;
      cursor: pointer;
      border-bottom: 1px solid #bababa; }
.rightBox .newList li:last-child {
        border-bottom: none; }
.rightBox .newList li:hover {
        color: #333; }
.rightBox .newList li a {
        color: #1e88e5; }
