/*定义普通按钮*/
.NormalBtn {
  display: inline-block;
  padding: 0 12px;
  line-height: 32px;
  height: 32px;
  font-size: 14px;
  color: #fff;
  text-align: center;
  text-decoration: none;
  outline: 0;
  border: 0 none;
  border-radius: 4px;
  background: #7babc2;
  -webkit-transition: background-color .2s ease-out;
  transition: background-color .2s ease-out;
  cursor: pointer; }
.NormalBtn:hover {
    background: #4aabc2; }
